<template>
  <section class="todo__edit grid h_max">
    <div class="edit__header shadow text_center grid grid_vertical_center">
      <edit-title :options="note"></edit-title>
      <button class="btn btn_radius btn_add" @click="contentAdd">+</button>
    </div>
    <div class="edit__body grid">
      <div class="card__body">
        <edit-form :content="note.content"></edit-form>
      </div>
      <div class="card__footer flex flex_end">
        <div class="button_group">
          <button v-if="!id" class="btn btn_big btn_warning" @click="noteSave">Добавить</button>
          <button v-if="id" class="btn btn_big btn_warning" @click="noteUpdate">Сохронить</button>
          <button v-if="id" class="btn btn_big btn_dark" @click="deleteShowModal">Удалить</button>
          <button class="btn btn_big btn_dark" @click="cancelShowModel">Отменить</button>
        </div>
      </div>
    </div>
    <modal :options="modalCancel" @down="cancelShowModel">
      <warning :options="warningCancel" @cancel="cancelShowModel" @confirm="cancelConfirm"></warning>
    </modal>
    <modal :options="deleteModal" @down="deleteShowModal">
      <warning :options="deleteWarning" @cancel="deleteShowModal" @confirm="deleteConfirm"></warning>
    </modal>
  </section>
</template>

<script>
import editForm from '@/components/editForm'
import editTitle from '@/components/editTitle'
import modal from '@/components/modal'
import warning from '@/components/warning'
export default {
  props: [
    'id'
  ],
  components: {
    'edit-form': editForm,
    'edit-title': editTitle,
    modal,
    warning
  },
  data () {
    return {
      note: null,
      modalCancel: {
        show: false
      },
      warningCancel: {
        message: 'Вы хотите покинуть эту страницу ?'
      },
      deleteModal: {
        show: false
      },
      deleteWarning: {
        message: 'Данная заметка будет удалена на всегда !'
      }
    }
  },
  methods: {
    /**
     * Метод ишет необходимую заметку по передонному props id и сохраняет её в локальную переменною note
     * если props отсутствует то иницеируются начальные значения note
     */
    initNote () {
      const list = this.$store.getters.getList
      if (this.id) {
        this.note = JSON.parse(JSON.stringify(list[this.id]))
        this.history.push(JSON.parse(JSON.stringify(this.note)))
      } else {
        this.note = {
          title: 'Новая заметка',
          content: [
            {
              text: 'Новая задача',
              status: false
            }
          ]
        }
      }
    },
    // добавляет задачю
    contentAdd () {
      this.note.content.push({ text: 'Новая задача', status: false })
    },
    // Вызвыает модальное окно для подтверждения выхода из редактора
    cancelShowModel () {
      this.modalCancel.show = !this.modalCancel.show
    },
    // Выход из редактора
    cancelConfirm () {
      this.$router.push('/')
    },
    // Вызывает модальное окно для подтверждения удаления заметки
    deleteShowModal () {
      this.deleteModal.show = !this.deleteModal.show
    },
    // Вызывает dispatch для удаления записи в firebase
    deleteConfirm () {
      this.$store.dispatch('DELETE_NOTE', this.id)
      this.$router.push('/')
    },
    // Вызывает dispatch для добовления записи в firebase
    noteSave () {
      this.$store.dispatch('ADD_NOTE', this.note)
      this.$router.push('/')
    },
    // Вызывает dispatch для обновления зависи в firebase
    noteUpdate () {
      this.$store.dispatch('UPDATE_NOTE', { note: this.note, id: this.id })
      this.history.push(JSON.parse(JSON.stringify(this.note)))
    }
  },
  created () {
    this.initNote()
  }
}
</script>

