<template>
  <div class="edit__input grid" :id="index">
    <div class="input__left grid">
      <label v-if="!showInput" @dblclick="openInput" for="" class="label__control">{{ options.text }}</label>
      <input v-if="showInput" @dblclick="openInput" type="text" v-model="options.text" class="control">
    </div>
    <div class="input__right grid grid_vertical_center grid_center">
      <input v-model="options.status" type="checkbox">
    </div>
    <div class="input__right grid grid_vertical_center grid_center">
      <button @click.prevent="contentDelete" class="icon-delete btn btn_radius btn_input"></button>
    </div>
  </div>
</template>

<script>
export default {
  props: [
    'index',
    'options'
  ],
  data () {
    return {
      showInput: false
    }
  },
  methods: {
    openInput () {
      this.showInput = !this.showInput
    },
    contentDelete () {
      this.$emit('delete:content', true)
    }
  }
}
</script>

<style lang="scss" scoped>
.edit__input {
  grid-template-columns: 5fr 1fr 1fr;
  border-bottom: 1px solid rgba(0, 0, 0, 0.137);
  padding: .5rem 1rem;
}
.control {
  font-size: 1rem;
  color: rgba(56, 56, 56, 0.459);
  background: none;
  outline: none;
  border: none;
}
.btn_input {
  font-size: 1rem;
  color: rgb(255, 124, 124);
}
</style>
