<template>
  <div class="title grid">
    <label @dblclick="open" v-if="show" class="title__text" for="title">{{ options.title }}</label>
    <input @dblclick="open" v-if="!show" class="title__input" id="title" type="text" v-model="options.title">
  </div>
</template>

<script>
export default {
  props: [
    'options'
  ],
  data () {
    return {
      show: true
    }
  },
  methods: {
    open () {
      this.show = !this.show
    }
  }
}
</script>

