<template>
  <div class="form grid">
    <ul>
      <li v-for="(item, index) in content" :key="index">
        <edit-input :options="item" @delete:content="del(index)"></edit-input>
      </li>
    </ul>
  </div>
</template>

<script>
import editInput from '@/components/editInput'
export default {
  props: [
    'content'
  ],
  components: {
    'edit-input': editInput
  },
  data () {
    return {
      list: this.content
    }
  },
  methods: {
    del (index) {
      this.list.splice(index, 1)
    }
  }
}
</script>
